import React, { useState } from "react";
import Select from "react-select";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import CloseIcon from "@mui/icons-material/Close";
import Circle from "@mui/icons-material/Circle";
import { grey, red, green, yellow } from "@mui/material/colors";

import {
  SortingState,
  IntegratedSorting,
  SearchState,
  IntegratedFiltering,
  RowDetailState,
  DataTypeProvider,
  PagingState,
  IntegratedPaging
} from "@devexpress/dx-react-grid";
import {
  Grid,
  Table,
  Toolbar,
  SearchPanel,
  TableHeaderRow,
  TableRowDetail,
  TableColumnVisibility,
  PagingPanel
} from "@devexpress/dx-react-grid-material-ui";

import "bootstrap/dist/css/bootstrap.min.css";

export default function IncidentManager(props) {
  const dataSiteUrl = "data/";

  const [companies, setCompanies] = useState([]);
  const [company, setCompany] = useState(null);
  const [group, setGroup] = useState(null);
  const [groupID, setGroupID] = useState(null);
  const [org, setOrg] = useState(null);
  const [serviceIDs, setServiceIDs] = useState([]);
  const [groups, setGroups] = useState([]);
  const [services, setServices] = useState([]);
  const priorities = [
    { label: "Low", value: "3" },
    { label: "Medium", value: "2" },
    { label: "High", value: "1" },
    { label: "Critical", value: "0" }
  ];
  const [selectedPriorities, setSelectedPriorities] = useState([
    { label: "Low", value: "3" },
    { label: "Medium", value: "2" },
    { label: "High", value: "1" },
    { label: "Critical", value: "0" }
  ]);
  const [rowDetailData, setRowDetailData] = useState({ WorkLog: [] });

  const [show4HrContainmentView, setShow4HrContainmentView] = useState(false);
  const [showAgeKpi, setShowAgeKpi] = useState(false);
  const [ageKpiBtnCss, setAgeKpiBtnCss] = useState("disabled");
  const [ageKpiArrowColor, setAgeKpiArrowColor] = useState("disabled");
  const [ageKpiOptionsCss, setAgeKpiOptionsCss] = useState("disabled");
  const [kpiDateField, setKpiDateField] = useState("Submit_Date");
  const [greenMaxAge, setGreenMaxAge] = useState(90);
  const [yellowMaxAge, setYellowMaxAge] = useState(180);
  const [redMaxAge, setRedMaxAge] = useState(240);
  const dropOffField = "Required_Resolution_DateTime";
  const dateFields = [
    { label: "Submit Date", value: "Submit_Date" },
    { label: "Last Modified", value: "Last_Modified_Date" },
    { label: "Last Resolved", value: "Last_Resolved_Date" },
    { label: "Reported Date", value: "Reported_Date" },
    { label: "Responded Date", value: "Responded_Date" },
    { label: "Closed Date", value: "Closed_Date" },
    { label: "Event Date", value: "Event_Date" },
    { label: "Required Resolution", value: "Required_Resolution_DateTime" },
    { label: "Estimated Resolution", value: "Estimated_Resolution_Date" },
    { label: "Acknowledgement Date", value: "Acknowledgment_Start_Date" },
    { label: "Resolution Start Date", value: "Resolution_Start_Date" },
    { label: "Last SLA Hold Date", value: "Last_SLA_Hold_Date" },
    { label: "VIP Assignment Date", value: "VIPAssignmentDateTime" },
    { label: "VIP Dispatch Date", value: "VIPDispatchDateTime" }
  ];

  const [columns, setColumns] = useState([
    { name: "Age_In_Minutes", title: "Age_In_Minutes" },
    { name: "Incident_Number", title: "Incident Number" },
    { name: "PriorityCode", title: "Priority" },
    { name: "Description", title: "Summary" },
    { name: "Submit_Date", title: "Submitted" },
    { name: "Last_Modified_Date", title: "Modified" },
    { name: "Assigned_Company_Group", title: "Company/Group" },
    { name: "Assignee", title: "Assignee" },
    { name: "ServiceCI", title: "ServiceCI" },
    { name: "StatusCode", title: "Status" },
    { name: "LastLog", title: "Last Log Entry" }
  ]);

  const [rows, setRows] = useState([]);

  React.useEffect(() => {
    if (company) {
      getGroups();
    }
    if (company) {
      getIncidents();
    }

    // check if 24 hr containment is still toggled
    if (
      selectedPriorities.length !== 2 ||
      selectedPriorities.filter((x) => x.label === "High").length !== 1 ||
      selectedPriorities.filter((x) => x.label === "Critical").length !== 1
    ) {
      setShow4HrContainmentView(false);
    }
  }, [company, groupID, serviceIDs, selectedPriorities]);

  React.useEffect(() => {
    getServices();
  }, [company, groupID, selectedPriorities]);

  React.useEffect(() => {
    getCompanies();
  }, []);

  React.useEffect(() => {
    addKpiColumn();
  }, [showAgeKpi]);

  React.useEffect(() => {
    console.log("t1:" + yellowMaxAge);

    if (showAgeKpi) {
      getIncidents();
      setShowAgeKpi(false);
      window.setTimeout(function () {
        setShowAgeKpi(true);
      }, 1);
    }
  }, [greenMaxAge, yellowMaxAge, redMaxAge, kpiDateField]);

  const addKpiColumn = () => {
    // remove
    setColumns(columns.filter((x) => x.title !== "Age"));

    if (showAgeKpi) {
      // add column in grid
      setColumns([{ getCellValue: getKpi, title: "Age" }].concat(columns)); // prepend to React State
    } else setShow4HrContainmentView(false);
  };

  React.useEffect(() => {
    if (
      ageKpiOptionsCss === "disabled" &&
      (kpiDateField !== "Submit_Date" ||
        greenMaxAge !== 90 ||
        yellowMaxAge !== 180 ||
        redMaxAge !== 240)
    ) {
      setShow4HrContainmentView(false);
    }

    if (ageKpiOptionsCss === "disabled") {
      //setShowAgeKpi(true);
    }
  }, [ageKpiOptionsCss]);

  React.useEffect(() => {
    // remove

    if (show4HrContainmentView) {
      setSelectedPriorities([
        { label: "High", value: "2" },
        { label: "Critical", value: "3" }
      ]);
      setKpiDateField("Event_Date");
      setGreenMaxAge(90);
      setYellowMaxAge(180);
      setRedMaxAge(240);
      setShowAgeKpi(true);

      columns
        .filter((x) => x.title === "Submitted")
        .forEach((column) => {
          column.title = "4-HR Start Date";
          column.name = kpiDateField;
        });

      // set service, company, group
      setCompany("All");
      setGroup("All");
      setGroupID("All");

      //todo: highlight stuff blue
    } else {
      columns
        .filter((x) => x.title === "4-HR Start Date")
        .forEach((column) => {
          column.title = "Submitted";
          column.name = "Submit_date";
        });
    }
  }, [show4HrContainmentView]);

  let getKpi = (data) => {
    console.log(yellowMaxAge);

    let _color = grey[500];
    if (data.Age_KPI.indexOf("green.gif") !== -1) _color = green[500];
    else if (data.Age_KPI.indexOf("yellow.gif") !== -1) _color = yellow[500];
    else if (data.Age_KPI.indexOf("red.gif") !== -1) _color = red[500];

    let _text = "";
    let _age = 0;
    try {
      let _age = parseInt(data.Age_In_Minutes);
      let _greenMaxAge = parseInt(greenMaxAge);
      let _yellowMaxAge = parseInt(yellowMaxAge);
      let _redMaxAge = parseInt(redMaxAge);

      if (_age > _redMaxAge) {
        _text = "Exceeded!";
      } else if (_age > _yellowMaxAge) {
        _text = "-" + (_redMaxAge - _age);
      } else if (_age > _greenMaxAge) {
        _text = "-" + (_yellowMaxAge - _age);
      } else {
        _text = "-" + (_greenMaxAge - _age);
      }
    } catch (ex) {}

    return (
      <div>
        <Circle sx={{ color: _color }} /> {_text}
      </div>
    );
  };

  const getKpiParams = () => {
    return `StartDateField%3D${kpiDateField};DropOffField%3D${dropOffField};GreenMaxAge%3D${greenMaxAge};YellowMaxAge%3D${yellowMaxAge};RedMaxAge%3D${redMaxAge}`;
  };

  const dateFormatter = (epoch) => {
    let date = new Intl.DateTimeFormat("en-US", {
      year: "numeric",
      month: "2-digit",
      day: "2-digit"
    }).format(epoch.value); // 01/11/2021
    return date;
  };

  const DateTypeProvider = (props) => (
    <DataTypeProvider formatterComponent={dateFormatter} {...props} />
  );

  const getCompanies = () => {
    fetch(`${dataSiteUrl}companies.json?opCode=GetCompanies`, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (json) {
        setCompanies([{ label: "All", value: "All" }].concat(json.data));
      });
  };

  const getGroups = () => {
    fetch(`${dataSiteUrl}groups.json?opCode=GetGroups&Company=${company}`, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (json) {
        setGroups([{ label: "All", value: "All|All" }].concat(json.data));
      });
  };

  const getServices = () => {
    if (show4HrContainmentView) {
      setServices([{ label: "CyberSecurity", value: "CyberSecurity" }]);
      setServiceIDs([{ label: "CyberSecurity", value: "CyberSecurity" }]);
    } else {
      fetch(
        `${dataSiteUrl}services.json?opCode=GetServices&Company=${company}&Org=${org}&GroupID=${groupID}&Priorities=${priorities.map(
          (x) => x.value
        )}&Filter=&KpiParams=${getKpiParams()}`,
        {
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json"
          }
        }
      )
        .then(function (response) {
          return response.json();
        })
        .then(function (myJson) {
          setServices(myJson.data);
        });
    }
  };

  const getIncidents = () => {
    fetch(
      `${dataSiteUrl}incidents.json?opCode=GetIncidents&Company=${company}&Org=${org}&GroupID=${groupID}&Priorities=${priorities.map(
        (x) => x.value
      )}&Services=${services.map(
        (x) => x.value
      )}&Filter=&KpiParams=${getKpiParams()}`,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json"
        }
      }
    )
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        setRows(myJson.data);
      });
  };

  const onCompanyChange = (val) => {
    setGroupID(null);
    setServiceIDs([]);
    setCompany(val.value);
  };

  const onGroupChange = (obj) => {
    setGroup(obj.value);
    if (obj.value.indexOf("|") !== -1) {
      setOrg(obj.value.split("|")[0]);
      setGroupID(obj.value.split("|")[1]);
    }
  };

  const onShowAgeKpiChange = (val) => {
    setShowAgeKpi(!showAgeKpi);
    if (!showAgeKpi) {
      setAgeKpiArrowColor("primary");
      setAgeKpiBtnCss("active");
    } else {
      // Style dropdown
      setAgeKpiArrowColor("disabled");
      setAgeKpiBtnCss("disabled");
      setAgeKpiOptionsCss("disabled");
    }
  };

  const onShowAgeKpiClick = () => {
    if (showAgeKpi) {
      setAgeKpiOptionsCss(
        ageKpiOptionsCss === "enabled" ? "disabled" : "enabled"
      );
      setAgeKpiBtnCss(ageKpiOptionsCss === "enabled" ? "active" : "selected");
    }

    if (ageKpiOptionsCss === "enabled") {
      //console.log(yellowMaxAge);
      getIncidents();
    }
  };

  const RowDetail = ({ row }) => {
    if (
      rowDetailData.Incident_Number == null ||
      rowDetailData.Incident_Number != row.Incident_Number
    ) {
      fetch(
        `${dataSiteUrl}incident.json?opCode=GetIncident&IncidentNumber=${row.Incident_Number}`,
        {
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json"
          }
        }
      )
        .then(function (response) {
          return response.json();
        })
        .then(function (json) {
          json.data.Incident_Number = row.Incident_Number;
          setRowDetailData(json.data);
        });
    }

    return (
      <div>
        <div class="detail-field">
          <strong>Summary:</strong>
          <br />
          {rowDetailData.Description}
        </div>
        <div class="detail-field">
          <strong>Priority:</strong>
          <br />
          {rowDetailData.PriorityCode}
        </div>
        <div class="detail-field">
          <strong>Notes:</strong>
          <br />
          {rowDetailData.Detailed_Decription}
        </div>
        <div class="detail-field">
          <strong>Customer/Office:</strong>
          <br />
          {rowDetailData.First_Name} {rowDetailData.Last_Name} /{" "}
          {rowDetailData.Department}
        </div>
        <div class="detail-field">
          <strong>Status:</strong>
          <br />
          {rowDetailData.StatusCode}
        </div>
        <div class="detail-field">
          <strong>Assigned Group:</strong>
          <br />
          {rowDetailData.Assigned_Group}
        </div>
        <div class="detail-field">
          <strong>Log Entries:</strong>
          <br />
          {rowDetailData.WorkLog.map(function (item, i) {
            return (
              <div>
                <div class="worklog-header">
                  {dateFormatter(item.Work_Log_Submit_Date)} /{" "}
                  {item.Work_Log_Submitter}
                </div>
                <div class="worklog-body">{item.Detailed_Description}</div>
              </div>
            );
          })}
        </div>
      </div>
    );
  };

  return (
    <div>
      <div class="filter-container">
        <form>
          <label class="label-nowrap">
            Company:
            <Select
              style={{ width: `140px` }}
              onChange={onCompanyChange}
              options={companies}
              value={companies.filter(function (option) {
                return option.value === company;
              })}
            ></Select>
          </label>
          <label>
            Group:
            <Select
              style={{ width: `320px` }}
              value={groups.filter(function (option) {
                return option.value === group;
              })}
              onChange={onGroupChange}
              options={groups}
              getOptionLabel={(x) => x.label}
              getOptionValue={(x) => x.value}
            ></Select>
          </label>
          <label>
            Services:
            <Select
              style={{ width: `320px` }}
              value={services.filter(function (service) {
                return serviceIDs.some((x) => service.value === x.value);
              })}
              onChange={(val) => {
                setShow4HrContainmentView(false);
                setServiceIDs((serviceIDs) => {
                  return val;
                });
              }}
              options={services}
              isMulti
            ></Select>
          </label>

          <label>
            Priorities:
            <Select
              value={priorities.filter(function (priority) {
                return selectedPriorities.some(
                  (x) => priority.value === x.value
                );
              })}
              onChange={(val) => {
                setSelectedPriorities((selectedPriorities) => {
                  return val;
                });
              }}
              options={priorities}
              class="form-check-input"
              isMulti
            ></Select>
          </label>
          <div style={{ paddingTop: 10 }}>
            <label>
              <div class="form-check form-switch">
                <input
                  class="form-check-input"
                  type="checkbox"
                  checked={show4HrContainmentView}
                  onChange={() =>
                    setShow4HrContainmentView(!show4HrContainmentView)
                  }
                />
                <label class="form-check-label" for="flexSwitchCheckDefault">
                  Show 4HR-Containment View
                </label>
              </div>
            </label>

            <label>
              <div class="form-check form-switch">
                <input
                  class="form-check-input"
                  type="checkbox"
                  checked={showAgeKpi}
                  onChange={onShowAgeKpiChange}
                />
                <label
                  onClick={onShowAgeKpiClick}
                  class={"form-check-label age-kpi-btn-" + ageKpiBtnCss}
                  for="flexSwitchCheckDefault"
                >
                  Show age indicators
                  <ArrowDropDownIcon color={ageKpiArrowColor} />
                  <div
                    onClick={(event) => event.stopPropagation()}
                    class={"age-kpi-options-" + ageKpiOptionsCss}
                  >
                    <span class="close-icon" onClick={onShowAgeKpiClick}>
                      <CloseIcon fontSize="small" />
                    </span>
                    <div class="label-nowrap">
                      Age field:&nbsp;
                      <label>
                        <Select
                          onChange={(val) => setKpiDateField(val.value)}
                          options={dateFields}
                          value={dateFields.filter(function (option) {
                            return option.value === kpiDateField;
                          })}
                        ></Select>
                      </label>
                      <br />
                      Green Max Age:&nbsp;
                      <label>
                        <input
                          value={greenMaxAge}
                          onInput={(e) => setGreenMaxAge(e.target.value)}
                          type="text"
                          class="form-control"
                        />
                      </label>
                      <br />
                      Yellow Max Age:&nbsp;
                      <label>
                        <input
                          value={yellowMaxAge}
                          onInput={(e) => setYellowMaxAge(e.target.value)}
                          type="text"
                          class="form-control"
                        />
                      </label>
                      <br />
                      Red Max Age:&nbsp;
                      <label>
                        <input
                          value={redMaxAge}
                          onInput={(e) => setRedMaxAge(e.target.value)}
                          type="text"
                          class="form-control"
                        />
                      </label>
                    </div>
                    <span class="footer">* in minutes</span>
                  </div>
                </label>
              </div>
            </label>
          </div>
        </form>
      </div>
      <div class="grid">
        <Grid rows={rows} columns={columns}>
          <PagingState defaultCurrentPage={0} pageSize={20} />
          <IntegratedPaging />
          <SortingState
            defaultSorting={[{ columnName: "Name", direction: "asc" }]}
          />
          <IntegratedSorting />
          <RowDetailState />
          <SearchState defaultValue="" />
          <IntegratedFiltering />
          <DateTypeProvider for={dateFields.map((x) => x.value)} />
          <Table />
          <TableColumnVisibility
            defaultHiddenColumnNames={["Age_In_Minutes"]}
          />
          <TableHeaderRow showSortingControls />
          <PagingPanel />
          <TableRowDetail contentComponent={RowDetail} />
        </Grid>
      </div>
    </div>
  );
}
